const tasks = [
    {
        title: 'C',
        complexity: 1,
        templates: [
            (a, b) => `  ${a}  ${b}.   `,
            (a, b) => `   ${a}    ${b}  .     ?`,
            (a, b) => `    ${a} ,     ${b} ,     ?`,
        ],
        params: () => [randomInt(5,40), randomInt(5,40)],
        solve: (a, b) => a + b,
    },
    {
        title: '',
        complexity: 1,
        templates: [
            (a, b) => `    ${a} ,    ${b},     ?`,
            (a, b) => `    ${a} ,    ${b} ,     ?`,
        ],
        params: () => {
            const a = randomInt(6, 50);
            const b = randomInt(5, a);
            return [a, b];
        },
        solve: (a, b) => a - b,
    },
    {
        title: ' x2',
        complexity: 2,
        templates: [
            (a, b, c) => `   ${a} .   ${b} ,    ${c} .     ?`,
        ],
        params: () => {
            const x = randomInt(1, 20);
            const b = randomInt(1, 20);
            const c = randomInt(1, 20);
            const a = x + b + c;
            return [a, b, c];
        },
        solve: (a, b, c) => a - b - c,
    },
    {
        title: ' x3',
        complexity: 2,
        templates: [
            (a, b, c) => `    ${a} .   ${b} ,    ${c}  ,      ?`,
        ],
        params: () => {
            const a = randomGet([20, 30, 40, 50]);
            const b = randomInt(5, 20);
            const c = randomInt(5, b);
            return [a, b, c];
        },
        solve: (a, b, c) => a - b + c,
    },
    {
        title: '',
        complexity: 2,
        templates: [
            (a) => `    ${a} ,    ?`
        ],
        params: () => [randomInt(1, 12)],
        solve: (a) => a * a,
    },
    {
        title: '',
        complexity: 2,
        templates: [
            (a, b) => `    ${a} /.    ,   ${b} ?`,
            (a, b) => `  ${b}   .      ${a} ,     ?`,
        ],
        params: () => {
            const a = randomInt(40, 80);
            const x = randomInt(2, 10);
            return [a, a * x];
        },
        solve: (a, b) => b / a,
    },
    {
        title: '',
        complexity: 2,
        templates: [
            (a, b) => ` 1    ${a} ,   ${b}  ?`,
            (a, b) => `      ${a} ,     ${b}    ,      ?`,
            (a, b) => `  ${b}   .      ${a} .     ?`,
            (a, b) => `    ${a}    ${b} ,   ?`,
        ],
        params: () => [randomInt(20, 50), randomInt(2, 10)],
        solve: (a, b) => a * b,
    },
    {
        title: '',
        complexity: 3,
        templates: [
            (a, b, c) => ` : ${a}x + ${b} = ${c}`
        ],
        params: () => {
            const a = randomInt(2, 10);
            const x = randomInt(1, 10);
            const b = randomInt(5, 30);
            const c = a * x + b;
            return [a, b, c];
        },
        solve: (a, b, c) => (c - b) / a,
    },
    {
        title: '',
        complexity: 4,
        templates: [
            (a, b) => `    ${a}    ${b} ,   ?`,
        ],
        params: () => [randomInt(2, 20), randomInt(2, 20)],
        solve: (a, b) => a + a + b + b,
    },
    {
        title: '',
        complexity: 6,
        templates: [
            (a, b, c, d) => `  ${a}/${b} ,    - ${c}/${d} .   ?`
        ],
        params: () => {
            const a = randomInt(1, 4);
            const b = randomInt(a + a, 10);
            const c = randomInt(1, 4);
            const d = randomInt(c + c, 10);
            return [a, b, c, d];
        },
        solve: (a, b, c, d) => {
            const x = a * d;
            const y = b * d;
            const z = c * b;
            return 1 - (x + z) / y;
        },
    },
];

import { randomInt, randomGet } from './utils.js';

function generateTask(index = null) {
    const task = index === null ? randomGet(tasks) : tasks[index];
    const inputs = task.params();
    const question = randomGet(task.templates)(...inputs);
    const answer = task.solve(...inputs);
    return {
        title: task.title,
        complexity: task.complexity,
        question,
        answer,
    };
}

export { generateTask };
